@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
:root {
    --primary-color: hsl(202, 33%, 29%);
    --primary-light: hsl(202, 33%, 45%);
    --text-light: hsl(0, 0%, 100%);
    --text-primary: hsl(202, 33%, 29%);
    --text-muted: hsla(202, 33%, 40%, 0.52);
    --background-color: hsl(34, 78%, 91%);
    --background-light: hsl(202, 33%, 90%);
    --background-lighter: hsl(202, 33%, 95%);
    --heat-gradient: linear-gradient(90deg, var(--primary-color), #950000);
    --shadow: 0 0 5px rgba(0, 0, 0, .7)
}

.dark-mode {
    --text-light: hsl(227, 100%, 89%);
    --text-primary: hsl(204, 48%, 62%);
    --text-muted: hsla(202, 33%, 70%, 0.52);
    --background-color: hsl(200, 90%, 8%);
    --background-light: hsl(200, 90%, 15%);
    --background-lighter: hsl(200, 90%, 25%);

    --heat-gradient: linear-gradient(90deg, var(--primary-color), #950000);
    --shadow: 0 0 5px rgba(0, 0, 0, .8)
}

.h-full {
    height: 100vh;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Lato, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    overflow-y: auto;
    color: hsl(202, 33%, 29%);
    color: var(--primary-color);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    max-width: 100vw;
