    -ms-user-select: none;
    user-select: none;
}

.Radio-container:hover input ~ .checkmark {
    background-color: var(--text-light);
}

.Radio-container input:checked ~ .checkmark {
    background-color: var(--primary-color);
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}


.Radio-container input:checked ~ .checkmark:after {
    display: block;
}

.Radio-container .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
}

